a{
    text-decoration: none;
    color: inherit;
}

.recipeCard {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    justify-content: left;
    background: #fff;
    padding:1em 1em 2em 1em;
    margin:1.5em;
    height: fit-content;
    width: fit-content;
  }
  
  .recipeCard:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }
  
  .content{
    padding: 0 0.3em 0.3em 0.3em;
    align-items: center;
    justify-content:space-evenly;
  }
  
  img{
    width: 100%;
  }
  
