
.form{
    min-height: 10vh;
    display: flex;
    align-items: center;
  }
  
  
  .search:focus,
  .button:focus{
    outline: none;
  }
  
  .search{
    width: 20em;
    background: #F2F1F9;
    border: black;
    padding: 0.5em;
    border-radius: 1em 0em 0em 1em;
  }
  
  .button{
    width: 5em;
    background: #BDC1AC;
    color: #fff;
    border: black;
    padding: 0.5em;
    border-radius: 0em 1em 1em 0em;
  }
  
  .App{
    min-height: 100vh;
    background: linear-gradient(#fff, #BDC1AC);
    font-family: 'Playfair Display', serif;
  }
  
  h1{
    padding: 1em;
    color: #BDC1AC;
  }
  
  .header{
    display:flex;
    margin-top: 1em;
    margin-right: 3em;
    justify-content: space-between;
  }
  
  .recipes{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  